import {Link} from 'gatsby'
import PropTypes from 'prop-types'
import React from 'react'

const Header = ({ siteTitle }) => (
  <div style={{ width: `100%`, maxWidth: `800px`, margin: `20px auto 0 auto`, paddingBottom: `20px` }}>
    <h2 style={{ display: `inline`, background: `#FFCA71`, padding: `0 10px`, color: `#ffffff`, fontSize: `1.5rem`, fontWeight: 600, textShadow: `1px 1px 3px rgba(0, 0, 0, .1)` }}>
      <Link to="/" style={{ textDecoration: `none` }} replace>
        {siteTitle}
      </Link>
    </h2>
  </div>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
